import React from 'react'
import { Link } from "react-router-dom"
import {MaterialUISwitch} from "./MatrialsIcons"

const Header = (props) =>{
  return(
  
        <nav className={ props.DarkMode ? "navbar navbar-expand-lg navbar-light bg-light" : "navbar navbar-expand-lg navbar-dark bg-dark" }>
    <div className="container-fluid">
      <Link to='/' className='navbar-brand'>SuperEditor</Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
          <li className="nav-item">
            <Link to='/' className="nav-link active" aria-current="page">Home</Link>
          </li>
          <li className="nav-item">
            <Link to='/about' className="nav-link" aria-current="page">About Us</Link>
          </li>
        </ul>
      </div>
    <MaterialUISwitch onClick={()=>props.ChangeDarkMode(data=>!data)} />
  </div>
  </nav>
    )
}

export default Header